<template>
  <div
    v-if="[1, 2].includes(viewType)"
    class="flex flex-col justify-center items-center h-screen w-screen gap-y-16 md:pb-64 px-4 pt-2">
    <div class="space-y-8">
      <img v-if="viewType === 1" src="@/assets/logo.svg" alt="Brand logo" class="mb-2 opacity-70 mx-auto" width="132" />
      <div
        class="space-y-4 w-full text-center"
        :style="{ maxWidth: `540px` }">
        <h2 class="text-5xl font-normal text-grey-04">We'll be back soon!</h2>
        <p class="text-base text-grey-02 leading-6">We are performing scheduled maintenance to serve you better. Please check back soon or reach out to us for assistance.</p>
      </div>
    </div>
    <div
      class="p-6 border border-grey-01 rounded-lg w-full space-y-6"
      :style="{ maxWidth: `880px` }">
      <div
        v-for="(description, n) in descriptions"
        :key="n"
        class="space-y-5">
        <span class="text-base font-semibold text-grey-04">{{ description.name }}</span>
        <ul class="list-disc px-4">
          <li
            v-for="(detail, m) in description.details"
            :key="`${n}_${m}`"
            class="space-y-1 text-orange-04">
            <div class="text-grey-04">
              <span class="font-semibold">{{ detail.name }}</span>
              <p class="text-grey-02">{{ detail.description }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div v-if="viewType === 1" class="text-center text-grey-02">
      For urgent inquiries, please go to
      <a href="https://help.reusely.com/" target="_blank">
        https://help.reusely.com/
      </a>
    </div>
  </div>
  <div v-else class="flex flex-col h-screen w-screen items-center justify-center">
    <p>{{ data.message }}</p>
    <h2 class="text-7xl">{{ data.code }}</h2>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, onUnmounted, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'

export default defineComponent({
  name: 'Error',
  props: {
    code: {
      type: [String, Number],
      default: 500
    },
    message: {
      type: String,
      default: 'Something went wrong!'
    }
  },
  setup() {
    const timerRetry = ref(null)
    const store = useStore()
    const route = useRoute()
    const router = useRouter()
    const data = computed(() => ({
      code: route?.params?.code,
      message: route?.query?.message
    }))
    const viewType = computed(() => {
      return (JSON.parse(window?.localStorage?.getItem('white-label')) ? 2 : 1) || 0
    })
    const retryLoginStatus = (time) => {
      clearTimeout(timerRetry)
      timerRetry.value = setTimeout(async () => {
        try {
          await store.dispatch('user/loginStatus', '/')
          router.push({
            path: '/'
          })
        } catch {
          retryLoginStatus(10000) /* 5 minute */
        }
      }, time)
    }
    const descriptions = computed(() => ([
      {
        name: 'System Status Updates',
        details: [
          {
            name: 'Maintenance in Progress',
            description: 'Estimated completion: Monday, January 27, 9:00 AM GMT'
          }
        ]
      }
    ]))

    onMounted(() => {
      if (data.value.code == 503) {
        retryLoginStatus(1000)
      }
    })
    onUnmounted(() => {
      clearTimeout(timerRetry.value)
    })

    return {
      data,
      viewType,
      descriptions
    }
  }
})
</script>